@import "../mixins";
.main{
  padding: 12.75em 0 5em;
  position: relative;
  &__top{
    max-width: 49.5em;
    @include grid100gap(1.5em);
    .display3{
      margin-top: -.1em;
    }
  }
  &__desc{
    max-width: 30em;
  }
  &__nav{
    display: flex;
  }
  &__btn{
    padding: 0 2.5em;
    margin-right: 1.75em;
  }
  &__img{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: -1;
    img{
      width: 100%;
      height: 100%;
      object-fit: cover;
      display: block;
    }
    ._view2{
      display: none;
    }
  }
}

.advantages{
  margin-top: 4em;
  max-width: 59em;
}

.discount{
  background-image: url(../img/discount-bg.webp);
  background-size: cover;
  background-position: center;
}

.calc{
  &__top{
    @include grid100gap(.5em);
  }
  &__grid{
    margin-top: 2.5em;
    display: grid;
    grid-template-columns: calc(100% - 27em) 25.25em;
    gap: 1.75em;
  }
  &__groups{
    @include grid100gap(1.5em);
  }
  &__step{
    display: flex;
    align-items: center;
    color: $gray;
    &:after{
      content: '';
      display: block;
      flex-grow: 1;
      margin-left: 1em;
      border-top: 1px dashed #D0D4DC;
    }
    span{
      flex-shrink: 0;
    }
  }
}

.calc-group{
  &__top{
    margin-bottom: 1.25em;
    display: flex;
    justify-content: space-between;
  }
  &__info{
    @include grid100gap(.625em);
    max-width: 38.75em;
  }
  &__nav{
    display: flex;
    align-items: center;
    align-self: flex-start;
  }
  &__slider{
    overflow: hidden;
  }
  .swiper{
    &-button{
      position: static;
      margin: 0 0 0 1em;
    }
    &-container{
      width: calc(100% + 1.75em);
    }
    &-slide{
      width: 25%;
      padding-right: 1.75em;
    }
  }
}

.calc-range{
  &__top{
    display: flex;
    align-items: center;
  }
  &__edit{
    margin-left: 1em;
    width: 1.25em;
    flex-shrink: 0;
    svg{
      width: 100%;
      height: auto;
    }
    &:hover{
      path{
        stroke: $primary;
      }
    }
  }
  &__num{
    font-variant-numeric: tabular-nums;
    -webkit-font-feature-settings: "tnum";
    font-feature-settings: "tnum";
  }
  &__input{
    margin-top: .375em;
    padding-top: .875em;
    padding-bottom: 3.25em;
    .noUi-target{
      box-shadow: none;
      border-radius: 0;
      border: none;
      height: unset;
    }
    .noUi-connects{
      background-color: #DEE2E9;
      height: 1px;
    }
    .noUi-connect{
      background: $red;
    }
    .noUi-handle{
      box-shadow: none;
      background-color: #fff;
      border: 3px solid #E31E24;
      width: 1.0625em;
      height: 1.0625em;
      border-radius: 50%;
      transform: translateX(-.5em);
      &:before,
      &:after{
        display: none;
      }
    }
    .noUi-pips-horizontal{
      height: unset;
      padding: 1.25em 0 0;
    }
    .noUi-marker-normal{
      display: none;
    }
    .noUi-marker-horizontal.noUi-marker-large{
      height: 1px;
      width: 4px;
      background: #798599;
      margin-top: calc(-1.25em - 1px);
    }
    .noUi-value{
      line-height: 150%;
      color: #798599;
      min-width: 1.0625em;
      text-align: center;
      &:nth-of-type(2){
        margin-left: .375em;
      }
      &:last-child{
        min-width: 1.375em;
        text-align: right;
        margin-left: -1em;
      }
    }
  }
}

.calc-info{
  position: sticky;
  top: 1em;
  padding: 2em;
  background: #F6F7F9;
  border-radius: .375em;
  @include grid100gap(1.5em);
  &__price{
    @include grid100gap(.625em);
  }
  &__form{
    border-top: $border;
    padding-top: 1.5em;
    @include grid100gap(1.5em);
  }
  &__btn{
    height: 3.9375em;
  }
  .m-check{
    padding: .1875em 0 0 .1875em;
  }
  &__more{
    a{
      text-decoration: underline;
      &:hover{
        text-decoration: none;
      }
    }
  }
}

.solutions{
  background-image: url(../img/solutions-bg.webp);
  background-size: cover;
  background-position: center;
  &__top{
    @include grid100gap(2.5em);
    color: #fff;
    max-width: 52.9375em;
    .noUi-value{
      color: #fff;
    }
    .calc-range__edit{
      path{
        stroke: #fff;
      }
    }
  }
  &__slider{
    margin-top: 2em;
    overflow: hidden;
  }
  &__nav{
    @include center;
    margin-top: 2em;
    .swiper-pagination{
      position: static;
      margin: 0 1em;
      width: unset;
    }
    .swiper-button{
      position: static;
      margin: 0;
    }
  }
}

.solutions-slider{
  width: calc(100% + 1.75em);
  &__item{
    padding-right: 1.75em;
    width: calc(100% / 3);
  }
}

.reviews{
  .display2{
    max-width: 19.67em;
  }
}

.info{
  @media screen and (min-width: 993px){
    &._reverse{
      .info__col{
        &:nth-child(2){
          order: -1;
        }
      }
    }
  }
}

.description{
  @include grid100gap(1.5em);
  &__by{
    display: flex;
    align-items: center;
    &-socials{
      display: flex;
      margin-left: .125em;
    }
    &-social{
      margin-left: .625em;
      width: 1.5em;
      svg{
        width: 100%;
        height: auto;
      }
      &:hover{
        path{
          fill: $red;
        }
      }
    }
  }
  &__tags{
    display: flex;
    flex-wrap: wrap;
    margin-top: -.25em;
  }
  &__counts{
    @include grid100gap(.75em);
  }
  &__nav{
    display: flex;
  }
  &__btn{
    padding: 0 2.5em;
  }
}

.description-count{
  display: flex;
  justify-content: space-between;
  &:before{
    content: '';
    display: block;
    flex-grow: 1;
    margin: 0 .625em .375em;
    border-top: 1px dashed #d0d4dc;
    align-self: flex-end;
  }
  .text16{
    line-height: 130%;
    max-width: 50%;
  }
  &__title{
    max-width: 60%;
    order: -1;
  }
  &__desc{
    max-width: 30%;
  }
}

.all-type{
  &__desc{
    margin-top: 2em;
  }
  .tabs{
    margin-top: 1.5em;
  }
}

.collection{
  &__top{
    @include grid100gap(.5em);
    max-width: 59em;
  }
  &__groups{
    margin-top: 2.5em;
    @include grid100gap(5em);
  }
  .info:nth-child(even){
    @media screen and (min-width: 993px){
      .info__col{
        &:nth-child(2){
          order: -1;
        }
      }
    }
  }
}

.order-consult{
  margin-top: 5em;
  padding: 3em 4em;
  background-image: url(../img/order-consult-bg.webp);
  background-size: cover;
  background-position: right top;
  border-radius: .375em;
  &__form{
    max-width: 43.25em;
    @include grid100gap(1.5em);
    &-grid{
      max-width: 32.125em;
      display: grid;
      grid-template-columns: calc(100% - 10.975em) 10em;
      gap: .875em;
    }
  }
}

.catalog-download{
  background-image: url(../img/catalog-bg.webp);
  background-size: cover;
  background-position: center;
  margin-top: 2.75em;
  color: #fff;
  position: relative;
  &__inner{
    display: flex;
    align-items: flex-end;
    justify-content: space-between;
  }
  &__form {
    max-width: 33.75em;
    @include grid100gap(1.5em);
  }
  .fg,
  &__btn{
    max-width: 21.25em;
  }
  &__images{
    position: absolute;
    right: 0;
    top: -12em;
    width: 76.625em;
  }
  &__magazine{
    width: 100%;
    display: block;
  }
  &__logo{
    position: absolute;
    left: 10em;
    top: 29.5625em;
    width: 6.5625em;
    display: block;
  }
}

.catalog-contain{
  position: relative;
  z-index: 2;
  width: 18.5em;
  padding: .5em;
  background: #FFFFFF;
  box-shadow: 0 0 5em rgba(121, 133, 153, 0.14);
  border-radius: .375em;
  color: $black;
  &__inner{
    padding: 1.5em;
    border: 1px solid #EAEBEE;
    border-radius: .1875em;
  }
  &__nav{
    margin-top: 1.5em;
    padding-top: 1.5em;
    border-top: $border;
  }
  &__link{
    display: flex;
    align-items: center;
    img{
      width: 1.5em;
      display: block;
      flex-shrink: 0;
      margin-right: .625em;
    }
  }
}

.pluses{
  background-image: url(../img/plusses-bg.webp);
  background-size: cover;
  background-position: center;
  color: #fff;
  &__grid{
    margin-top: 2.5em;
  }
  &__items{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .875em));
    gap: 2.5em 1.75em;
  }
  &__item{
    display: flex;
    &:before{
      content: '';
      display: block;
      width: 3em;
      height: 3em;
      background-image: url(../img/check3.svg);
      @include bgImgSize(100%);
      flex-shrink: 0;
      margin-right: 1em;
    }
    &._full{
      grid-column: 1/-1;
    }
  }
}

.pluses-slider{
  position: relative;
  overflow: hidden;
  &__img{
    @include imgCover(56.1%);
    img{
      border-radius: .3125em;
      display: block;
    }
  }
  @media screen and (min-width: 993px){
    .swiper-pagination{
      bottom: 1.5em;
      &-bullet{
        width: .5em;
        height: .5em;
        background-color: #fff;
        &-active{
          background: $red !important;
          border-color: $red !important;
        }
      }
    }
  }
}

.about{
  .display2{
    max-width: 20.875em;
  }
}

.canvas-slider,
.certificates-slider{
  position: relative;
  .swiper{
    &-container{
      width: calc(100% + 1.75em);
      overflow: hidden;
    }
    &-wrapper{
      align-items: stretch;
    }
    &-slide{
      height: auto;
      padding-right: 1.75em;
    }
    &-button{
      &-prev{
        left: -5em;
      }
      &-next{
        right: -5em;
      }
    }
    &-pagination{
      display: none;
    }
  }
}

.canvas-slider{
  .swiper{
    &-slide{
      width: calc(100% / 3);
    }
  }
}

.certificates{
  margin-top: 5em;
  &__top{
    max-width: 65.75em;
    @include grid100gap(1.5em);
  }
}

.certificates-slider{
  margin-top: 2.5em;
  .swiper{
    &-slide{
      width: 25%;
    }
  }
}

.guarantee{
  margin-top: 5em;
  display: grid;
  grid-template-columns: 25.25em calc(100% - 27em);
  gap: 1.75em;
  &__text{
    @include grid100gap(1.5em);
  }
  &__title{
    font-weight: 700;
    font-size: 5em;
    line-height: 120%;
  }
  &__desc{
    margin-top: 1em;
  }
  &__ceo{
    position: relative;
  }
}

.ceo-info{
  position: absolute;
  width: 17.75em;
  max-width: 100%;
  bottom: 2em;
  left: 0;
  background: #FFFFFF;
  border-radius: 0 .375em .375em 0;
  padding: 1.5em;
  @include grid100gap(.5em);
}

.impression{
  background-image: url(../img/impression.webp);
  background-size: cover;
  background-position: center;
  color: #fff;
  &__inner{
    width: calc(50% - .875em);
    margin-left: auto;
    @include grid100gap(2.5em);
  }
  &__items{
    @include grid100gap(2.5em);
  }
}


@media screen and (max-width: 992px){
  .main{
    padding: 1em 0 2em;
    &__top{
      max-width: 33.5em;
    }
  }

  .calc{
    display: none;
  }

  .calc-range{
    &__input {
      padding-bottom: 3em;
      .noUi-pips-horizontal{
        padding-top: .75em;
      }
    }
  }

  .calc-info{
    &__price{
      .display3{
        font-size: 2.25em;
      }
    }
  }

  .solutions{
    &__top{
      gap: 1.5em;
    }
    &__slider{
      margin-top: 1.5em;
    }
  }

  .solutions-slider{
    &__item{
      width: 50%;
    }
  }

  .description{
    .calc-info__price{
      .text20{
        font-size: 1.125em;
      }
    }
  }

  .all-type{
    .tabs{
      margin-top: 1em;
    }
  }

  .collection{
    padding-bottom: 0;
    &__groups{
      margin-top: 1.5em;
      gap: 2em;
    }
  }

  .order-consult{
    background: #F4F5F8;
    margin: 2em 0 0 -1em;
    width: calc(100% + 2em);
    padding: 2em 1em;
    .display3{
      font-size: 1.875em;
    }
  }

  .catalog-download{
    margin-top: 0;
    padding: 2em 0 0;
    overflow: hidden;
    background: unset;
    .container{
      background-image: url(../img/catalog-bg.webp);
      background-size: cover;
      background-position: center;
      padding-top: 31.5em;
      padding-bottom: 2em;
    }
    &__logo{
      top: 14.875em;
    }
    &__images{
      top: 0;
      width: 100%;
    }
  }

  .catalog-contain{
    display: none;
  }

  .pluses{
    &__grid{
      margin-top: 1.5em;
    }
  }

  .pluses-slider{
    .swiper-pagination{
      position: static;
      margin-top: 1em;
    }
  }

  .canvas-slider,
  .certificates-slider{
    .swiper {
      &-container {
        width: calc(100% + 1em);
      }
      &-slide{
        padding-right: 1em;
      }
      &-button{
        display: none;
      }
      &-pagination{
        display: flex;
        position: static;
        width: 100%;
        margin-top: 1em;
      }
    }
  }

  .canvas-slider{
    .swiper{
      &-slide{
        width: 50%;
      }
    }
  }

  .certificates{
    margin-top: 2em;
    &__top{
      gap: 1em;
    }
    .display4{
      font-size: 1.875em;
    }
  }

  .certificates-slider{
    margin-top: 1em;
    .swiper{
      &-slide{
        width: calc(100% / 3);
      }
    }
  }

  .guarantee{
    margin-top: 2em;
    grid-template-columns: 100%;
    gap: 1.5em;
    &__text{
      gap: .5em;
    }
    &__title{
      font-size: 4em;
    }
    &__desc{
      margin-top: 1.14em;
    }
  }

  .impression{
    background-image: url(../img/impression2.webp);
    &__inner{
      width: 100%;
    }
    &__inner,
    &__items{
      gap: 1.5em;
    }
  }
}


@media screen and (max-width: 576px){
  .main{
    &__top{
      gap: 1em;
      .display3{
        margin: 0;
      }
    }
    &__nav{
      @include grid100gap(.75em);
    }
    &__btn{
      margin: 0;
    }
    &__img{
      position: relative;
      height: auto;
      width: calc(100% + 2em);
      margin: -4.6875em 0 0 -1em;
      img{
        height: auto;
      }
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
    }
  }

  .advantages{
    width: calc(100% + 2em);
    margin: 2em 0 0 -1em;
  }

  .solutions{
    &__nav{
      display: none;
    }
  }

  .solutions-slider{
    width: 100%;
    &>.swiper-wrapper{
      transform: unset !important;
    }
    &__item{
      width: 100%;
      padding: 0;
    }
  }

  .description{
    &__tags{
      margin-top: -.75em;
    }
    &__by,
    &__counts,
    &__list{
      display: none;
    }
    &__btn{
      padding: 0;
      width: 100%;
    }
  }

  .order-consult{
    &__form{
      &-grid{
        grid-template-columns: 100%;
      }
    }
  }

  .catalog-download{
    .container{
      padding-top: 28.5em;
    }
    &__images{
      top: 0;
      width: 100%;
      height: 28.5em;
    }
    &__magazine{
      height: 135%;
      width: auto;
      position: absolute;
      left: 50%;
      top: -22%;
      transform: translateX(-50%);
    }
    &__logo{
      left: unset;
      right: 2.3125em;
    }
    .display3{
      font-size: 1.875em;
    }
  }

  .pluses{
    &__items{
      grid-template-columns: 100%;
      gap: 1.5em;
    }
  }

  .certificates-slider,
  .canvas-slider{
    .swiper{
      &-container{
        width: 100%;
      }
      &-slide{
        width: 100%;
        padding: 0;
      }
    }
  }

  .guarantee{
    .video{
      &__img{
        @include imgCover(143%);
      }
    }
  }

  .impression{
    padding-bottom: 14.6875em;
  }
}
